#towerSelection {
  flex: 1;
  justify-content: center;
  font-family: Kanit, sans-serif;
  display: none;
}

#towerSelection .tower {
  width: 25%;
  background-color: #0000004d;
  border-style: solid;
  border-width: thin;
  border-radius: 6px;
  flex-direction: row;
  gap: 10%;
  margin-right: 1%;
  padding: 5px;
  font-size: 48px;
  display: flex;
  position: relative;
}

#towerSelection .tower.selected {
  background-color: #00000080;
  border-style: solid;
  border-radius: 6px;
  font-weight: bold;
}

#towerSelection .tower .towerHoverText {
  visibility: hidden;
  width: max-content;
  color: #fff;
  text-align: center;
  z-index: 1;
  opacity: 0;
  background-color: #000;
  border-radius: 6px;
  padding: 5px;
  font-size: 16px;
  transition: opacity 1s ease-in-out;
  position: absolute;
  top: 0%;
  left: 25%;
  transform: translate(-25%, -100%);
}

#towerSelection .tower:hover .towerHoverText {
  visibility: visible;
  opacity: 1;
}

#towerSelection .tower .imageOuter {
  text-align: center;
  flex-direction: column;
  flex-shrink: 1;
  justify-content: center;
  display: flex;
}

#towerSelection .tower .imageOuter .towerIcon {
  height: auto;
  max-height: 100%;
  max-width: 100%;
  margin: auto;
  display: block;
}

#towerSelection .tower .imageOuter .towerName {
  color: #fff;
  font-size: 30%;
}

#towerSelection .tower .towerInfo {
  max-width: 100%;
  height: auto;
  flex: 1;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  display: grid;
}

#towerSelection .tower .towerInfo .cost.armor {
  grid-area: 1 / 2;
}

#towerSelection .tower .towerInfo .cost.makes-money {
  grid-area: 2 / 1 / auto / 2;
}

#towerSelection .tower .towerInfo .cost {
  width: 50%;
  align-items: center;
  font-size: 45%;
  font-weight: bold;
  display: flex;
}

#towerSelection .tower .towerInfo .cost .towerInfoIcon {
  max-height: 100%;
  max-width: 100%;
}

#towerSelection .tower .towerInfo .cost .costText {
  text-shadow: -1px 1px #fff, 1px 1px #fff, 1px -1px #fff, -1px -1px #fff;
}

#moneyContainer {
  visibility: hidden;
  height: 100%;
  width: 15%;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr;
  place-items: center;
  display: grid;
  position: relative;
}

#moneyImage {
  image-rendering: pixelated;
  z-index: 1;
  grid-area: 1 / 1;
  transform: scale(1.7);
}

#moneyDisplay {
  z-index: 2;
  text-shadow: -1px 1px #fff, 1px 1px #fff, 1px -1px #fff, -1px -1px #fff;
  grid-area: 1 / 1;
  margin-top: 20%;
  font-family: Kanit, sans-serif;
  font-size: 1.7rem;
}

* {
  box-sizing: border-box;
}

#reactUI {
  height: 14%;
  width: 166vh;
  justify-content: center;
  padding-left: 5%;
  padding-right: 10%;
  display: flex;
  position: absolute;
  bottom: 1%;
}

